<template>
  <div class="wrapper" v-if="users.length > 0">
    <header>
      <h1 v-if="users.length > 0">{{actualUser.username}}</h1>
      <select v-model='idUserProp'>
        <option v-for="(opt) in users" :value='opt.id' v-bind:key='opt.id'>{{opt.username}}</option>
      </select>
    </header>
    <!-- <div id="create" v-if="users.find(usr => usr._id == (idUserProp || idUser)).role == 'ind'"> -->
    <div id="create">
      <hr>
      <h2>Criar Crédito</h2>
      <form action="">
        <label for="nome">Nome:</label>
        <input @input="getName($event.target.value)" name='nome' type="text" v-model="createName" placeholder="Digite aqui o nome do seed">
        <br>
        <label for="tipo">Tipo:</label>
        <input type="radio" v-model="type" id="reais" name="tipo" value="MONETARY">
        <label for="reais">Reais (R$)</label>
        <input type="radio" v-model="type" id="promo" name="tipo" value="PROMOTIONAL">
        <label for="promo">Pontos</label>
        <br>
        <label for="valor">Valor:</label>
        <vue-autonumeric v-model="createVal"
        :options="{
          digitGroupSeparator: '.',
          decimalCharacter: ',',
          decimalCharacterAlternative: '.',
          currencySymbol: 'R$',
          }"
          :placeholder="'Digite o valor'"
          v-if="type == 'MONETARY'"
        ></vue-autonumeric>
        <input v-else name="valor" type="number" step="1" onkeypress="return event.charCode >= 48 && event.charCode <= 57" v-model="createVal" placeholder="Digite o valor">
        <br>
        <button v-on:click="createSeed">Criar</button>
      </form>
    </div>
    <template v-if="Object.keys(actualUser).length > 0">
      <div id="frac" v-if="(Object.keys(actualUser.seeds).length > 0) && (seeds.length > 0)">
        <hr>
        <h2>Transferir Crédito</h2>
        <form action="">
          <label for="seed">Crédito:</label>
          <select v-model="fracIdCamp">
            <option v-for="doc in actualUser.seeds" v-if="(doc.emitterId == idUserProp) || (doc.ownerId == idUserProp)" :value="doc.docId">{{doc.description}}</option>
          </select>
          <br>
          <label for="dest">Destino:</label>
          <select v-model="fracIdUser">
            <!-- <option v-for="(merc) in users.filter(usr => usr.id != idUserProp)" v-bind:key="merc.id" :value="merc.id">{{merc.username}}</option> -->
            <option v-for="(merc) in users" v-bind:key="merc.id" :value="merc.id">{{merc.username}}</option>
          </select>
          <br>
          <label for="valor">Valor:</label>
          <vue-autonumeric v-model="fracValue"
            :options="{
              digitGroupSeparator: '.',
              decimalCharacter: ',',
              decimalCharacterAlternative: '.',
              currencySymbol: 'R$',
              }"
              :placeholder="'Digite o valor'"
              v-if="actualUser.seeds.find(id => id.docId == fracIdCamp).type == 'MONETARY'"
            ></vue-autonumeric>
          <input v-else name="valor" type="number" step="1" onkeypress="return event.charCode >= 48 && event.charCode <= 57" placeholder="Digite o valor" v-model="fracValue">
          <br>
          <button v-on:click.prevent="fracSeed">Transferir</button>
        </form>
      </div>
      <div id="saldo" v-if="actualUser.seeds.length > 0 && seeds.length > 0">
        <hr>
        <h2>Saldo</h2>
        <label for="seed">Selecione o Crédito:</label>
        <select v-model="frac">
          <option v-for="seed in actualUser.seeds" :value="seed.docId">{{seed.description}}</option>
        </select>
        <div class="results" v-if="1 == 1">
          <div class="valores" v-if="(actualUser.seeds.find(au => au.created == true)) || (actualUser.seeds.find(bl => bl.value != bl.balance))">
              <h4>Total do Crédito:</h4>
              <span>{{parseFloat(actualUser.seeds.find(au => au.docId == frac).value).toLocaleString('pt-BR', (actualUser.seeds.find(au => au.docId == frac).type == 'PROMOTIONAL' ? {} : {style: 'currency', currency: 'BRL', minimumFractionDigits:2, maximumFractionDigits:2}))}}</span>
              <span v-if="actualUser.seeds.find(au => au.docId == frac).type == 'PROMOTIONAL'"> Pontos</span>
          </div>
          <div class="valores">
              <h4>{{(actualUser.seeds.find(au => au.created == true)) || (actualUser.seeds.find(bl => bl.value == 1)) ? 'Parcial do Crédito:' : 'Total do Crédito:'}}</h4>
              <span>{{parseFloat(actualUser.seeds.find(au => au.docId == frac).balance).toLocaleString('pt-BR', (actualUser.seeds.find(au => au.docId == frac).type == 'PROMOTIONAL' ? {} : {style: 'currency', currency: 'BRL', minimumFractionDigits:2, maximumFractionDigits:2}))}}</span>
              <span v-if="actualUser.seeds.find(au => au.docId == frac).type == 'PROMOTIONAL'"> Pontos</span>
          </div>
          <br>
        </div>
      </div>
      <div id="cashout" v-if="(actualUser.seeds.length > 0) && (actualUser.seeds.find(bl => bl.balance))">
        <hr>
        <h2>Usar Crédito</h2>
        <vue-autonumeric v-model="cashValue"
        :options="{
          digitGroupSeparator: '.',
          decimalCharacter: ',',
          decimalCharacterAlternative: '.',
          currencySymbol: 'R$',
          }"
          :placeholder="'Digite o valor'"
          v-if="actualUser.seeds.find(id => id.docId == frac).type == 'MONETARY'"
        ></vue-autonumeric>
        <input v-else name="valor" type="number" step="1" onkeypress="return event.charCode >= 48 && event.charCode <= 57" placeholder="Digite o valor" v-model="cashValue">
        <br>
        <button v-on:click.prevent="cashout">Usar</button>
      </div>
    </template>
  </div>
</template>

