        var arr1 = arrFull,
        merged = [];

        [arr1].forEach((hash => a => a.forEach(b => {
          if (!hash[(b.seed_id || b.participated_seeds_id)]) {
            hash[(b.seed_id || b.participated_seeds_id)] = {};
            merged.push(hash[(b.seed_id || b.participated_seeds_id)]);
          }
          Object.assign(hash[(b.seed_id || b.participated_seeds_id)], b);
        }))(Object.create(null)));


        return merged;
      },
