        //   "content": [
        //     {
        //       "active": true,
        //       "id": Math.max.apply(null, (this.seeds.map(iid => iid.content[0].id).length > 0 ? this.seeds.map(iid => iid.content[0].id) : [0]))+1,
        //       "type": this.type,
        //       "status": "UNSUED",
        //       "emitter": {
        //         "id": this.users.find(usr => usr.id == this.idUserProp).id,
        //         "username": this.users.find(usr => usr.id == this.idUserProp).username,
        //         "active": true,
        //       },
        //       "description": this.message,
