        //       "parentId": '',
        //       "emitterId": this.users.find(usr => usr.id == this.idUserProp).id,
        //       "ownerId": this.users.find(usr => usr.id == this.idUserProp).id,
        //       "status": "UNSUED",
        //       "value": (this.type == 'PROMOTIONAL' ? parseInt(this.createVal) : this.createVal),
        //       "balance": (this.type == 'PROMOTIONAL' ? parseInt(this.createVal) : this.createVal),
        //       "link": ""
        //     }
        //   ]
        // }

        // MLAB
        // await fetch(this.aSeedUrl + this.apiKey, {

        await fetch(this.apiSeeds, {
          method: 'POST',
          body: JSON.stringify(seed),
          headers: new Headers({
            'Authorization': this.resToken,
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          }),
        })
        .then(response => {
