        //       "link": ""
        //     }
        //   ]
        // }

        var fracAltera = this.docs.find(dv => dv.id == this.fracIdCamp).id
        // var fracValor = this.docs.find(dv => dv.id == this.fracIdCamp).balance

        // MLAB APENAS
        //  var updateDoc = {
        //   "content": [
        //     {
        //       "active": true,
        //       "id": this.docs.find(dv => dv.content[0].id == this.fracIdCamp).content[0].id,
        //       "seedId": this.docs.find(dv => dv.content[0].id == this.fracIdCamp).content[0].seedId,
        //       "parentId": this.docs.find(dv => dv.content[0].id == this.fracIdCamp).content[0].parentId,
        //       "emitterId": this.docs.find(dv => dv.content[0].id == this.fracIdCamp).content[0].emitterId,
        //       "ownerId": this.docs.find(dv => dv.content[0].id == this.fracIdCamp).content[0].ownerId,
        //       "status": "UNSUED",
        //       "value": this.docs.find(dv => dv.content[0].id == this.fracIdCamp).content[0].value,
        //       "balance": fracValor - (this.actualUser.seeds.find(id => id.docId == this.fracIdCamp).type == 'PROMOTIONAL' ? parseInt(this.fracValue) : this.fracValue),
        //       "link": this.docs.find(dv => dv.content[0].id == this.fracIdCamp).content[0].link
        //     }
        //   ]
        // }


        await fetch(this.apiDocs + '/' + fracAltera + '/fractionate' , {
          method: 'POST',
          body: JSON.stringify(fracDoc),
          headers: new Headers({
            'Authorization': this.resToken,
            'Accept': 'application/json',
            'Content-Type': 'application/json'
