        // .then(response => {
        //   alert('Atualizou Document Original');
        //   // console.log('diminuiu valor do original ==> ', response)
        // })

        await this.reloadAllCards();
      },
      async cashout(e) {
        // MLAB

        var fracAltera = this.docs.find(dv => dv.content[0].id == this.frac)._id
        var fracValor = this.docs.find(dv => dv.content[0].id == this.frac).content[0].balance

         var updateDoc = {
          "content": [
            {
              "active": true,
              "id": this.docs.find(dv => dv.content[0].id == this.frac).content[0].id,
              "seedId": this.docs.find(dv => dv.content[0].id == this.frac).content[0].seedId,
              "parentId": this.docs.find(dv => dv.content[0].id == this.frac).content[0].parentId,
              "emitterId": this.docs.find(dv => dv.content[0].id == this.frac).content[0].emitterId,
              "ownerId": this.docs.find(dv => dv.content[0].id == this.frac).content[0].ownerId,
              "status": "UNSUED",
              "value": this.docs.find(dv => dv.content[0].id == this.frac).content[0].value,
              "balance": (fracValor) - (this.actualUser.seeds.find(id => id.docId == this.frac).type == 'PROMOTIONAL' ? parseInt(this.cashValue) : this.cashValue),
              "link": this.docs.find(dv => dv.content[0].id == this.frac).content[0].link
            }
          ]
