        // // MLAB APENAS
        await fetch(this.aDocUrl + '/' + fracAltera.$oid + this.apiKey, {
          method: 'PUT',
          body: JSON.stringify(updateDoc),
          headers: new Headers({'content-type': 'application/json'}),
